@keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
}

@keyframes nuage1 {
  0%{
    left:-228px;
    opacity:0.9;
  }

  98% {
    left:100%;
    opacity:0.9;
  }

  99% {
    left:100%;
    opacity:0;
  }

  100%{
    left:-228px;
    opacity:0;
  }  
}

@keyframes nuage2 {
  0%{
    left:40%;
    opacity:0.9;
  }

  50% {
    left:100%;
    opacity:0.9;
  }

  51% {
    left:100%;
    opacity:0;
  }

  52%{
